@import url(https://fonts.googleapis.com/css?family=Raleway:300&display=swap);
body{margin:0;padding:0;background-color:#333;min-height:100vh;width:100vw;overflow:hidden}a{text-decoration:none;color:#e6e6e6;margin:0 10px}.active{color:rgba(26,162,230,0.8);border-bottom:3px solid rgba(26,162,230,0.8)}

.app{font-family:'Raleway', sans-serif;display:grid;grid-template-columns:repeat(3, auto);grid-template-rows:auto;justify-content:center;align-content:center;grid-gap:10px;gap:10px;padding-top:50px}.app>div:nth-child(5)>div{background-image:linear-gradient(45deg, rgba(26,162,230,0.8) 0%, rgba(51,51,204,0.8) 100%)}.app>div:nth-child(5)>div .modal-message{background-image:linear-gradient(45deg, rgba(94,230,26,0.6) 0%, rgba(51,204,102,0.6) 100%)}.app__nav{display:flex;flex-wrap:wrap;background-color:rgba(0,0,0,0.2)}.app__nav a{margin:0 10px;padding:5px}.modal-window{position:relative;display:flex;justify-content:center;align-items:center;width:120px;height:120px;background-color:rgba(77,77,77,0.4);box-shadow:0 0 40px 0 #1a1a1a}.modal-message{display:flex;justify-content:center;align-items:center;padding:10px 10px;border:1px solid #e6e6e6;color:#e6e6e6;font-size:.8rem;cursor:default}

